.ProgressBar {
  width: 100%;
  height: 100%;
}

.ProgressBar-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.ProgressBar-time {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: center;
  background: #000;
  color: #ffffff;
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.ProgressBar-time.invert {
  color: #000;
  background: #fff;
  z-index: 2;
}



